.calendar {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  margin-top: 40px;
  width: $width;
  gap: $gap;
  padding: $padding;

  &__block {
    width: $size;
    height: $size;
    border: 1px solid black;
    background-color: #eee;
    transition: 0.5s;
    perspective: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;

    &:hover {
      background: #ffbfcb;
      cursor: pointer;
      transform: translate(0, -20px);
    }

    @for $index from 1 through 31 {
      &:nth-child(#{$index}) {

        &::before {
          content: "#{$index}";
        }
      }
    }

    @each $day,
      $day-number in $week {
      &--start-day-#{$day} {
        margin-left: $day-number;
      }
    }
  }

  @for $days from 28 through 31 {
    &--month-length-#{$days} :nth-child(n + #{$days + 1}) {
      display: none;
    }
  }
}
